@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
    font-size: 14px;
    color: #565f6f;
    background: #676767;
    font-family: 'Roboto', Arial, sans-serif;
}
.wrapper {
    margin: 30px 0;
}
.container {
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 6px rgba(0,0,0,0.15);
    padding: 25px;
}
/* Page Header */
.page-header {
    border-bottom: 3px solid #e1e1e1;
    margin-bottom: 30px;
}
.page-header h2 {
    color: #404954;
    font-size: 3rem;
    font-weight: 600;
    letter-spacing: -0.1rem;
    display: inline-block;
    margin-top: 0;
}
.page-header h3 {
    color: #404954;
    font-weight: 600;
}
.page-header .lead {
    font-size: 1.5rem;
    font-weight: 200;
    margin: 0;
    max-width: 85%;
    padding-bottom: 15px;
}
.page-header .view-all {
    display: inline-block;
    float: right;
    text-transform: uppercase;
}
section.news {
    padding: 30px 0;
}
a {
    text-decoration: none !important;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

section.news .news_item {
    border: 2px solid #006ec5;
    border-radius: 4px;
    padding: 12px;
    margin-bottom: 20px;
}

section.news .news_item_img img {
    max-width: 100%;
}

section.news .news_item_title {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.2;
    margin: 10px 0;
    display: block;
    color: #046992;
    text-transform: uppercase;
}

section.news .news_item .news_inform_wrap {
    height: 170px;
    overflow: hidden;
    margin-bottom: 10px;
}

section.news .news_item a.button {
    color: #fff;
    border-radius: 4px;
    padding: 12px 20px;
    background-color: #2e6be6;
    display: block;
    text-align: center;
}

section.news .news_item a.button:hover {
    background-color: #2655b3;
}

